<template>
  <div>
    <h1>Bitte stehen Sie bereit</h1>
  </div>
</template>
<script>
import axios from 'axios';

export default {
  data: () => ({
    token: '',
  }),
  created() {
    this.tokenName = '_sw_payment_token';
    this.token = this.$route.query[this.tokenName];

    let queryString = '';

    // eslint-disable-next-line no-restricted-syntax
    for (const key in this.$route.query) {
      if (Object.prototype.hasOwnProperty.call(this.$route.query, key)) {
        queryString += `${key}=${this.$route.query[key]}&`;
      }
    }

    axios
      .get(`http://77.245.20.254/store-api/v3/axonlab/payment/finalize-transaction?${queryString}`,
        {
          headers: {
            'sw-access-key': 'SWSCAUPEWFJVU0FVC20WCNDLUG',
            'sw-context-token': localStorage.getItem('contextToken'),
          },
        })
      .then((response) => {
        window.location.replace(response.data.finishUrl);
      });
  },
};
</script>
